






/* eslint-disable vuejs-accessibility/form-control-has-label */

import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

@Component({})
export default class Btn extends Vue {
    @Prop({
      default: '',
    })
    private text!: string;

    click(event: MouseEvent): void {
      this.$emit('click', event);
    }
}
